// components / dashboard / index

// Modules
import React from "react";
import {connect} from 'react-redux';
import Link, {navigateTo} from "gatsby-link";

// Styles
// import styles from '../../style/dashboard.module.css';
// import styles from '../../style/theme.css';

// Action
// import {getAllMessages, getMessageChat} from '../../../action/MessageAction.js';
import {setUser, logOutUser, setUserExperience} from '../../action/UserAction';

// Components
import Footer from './Footer';
import Navigation from './Navigation';
import Sidebar from './Sidebar';

class DashboardIndex extends React.Component {
    componentDidMount() {
        let userToken = localStorage.getItem("user");
        if (!userToken) {
            this.props.logOutUser().then((result) => {
                if (result) {
                    localStorage.clear();
                    navigateTo('/login');
                }
            });
        } else {
            this.props.setUser({"token": userToken});
        }
    }

    componentDidUpdate() {
        // init system if it haven't been set
        // if(!this.props.user_experience.systemInit) {
        //     this.inspiniaInit();
        //     this.props.setUserExperience({
        //         systemInit: true
        //     });
        //     console.log('test');
        // }
        this.inspiniaInit();
    }

    inspiniaInit() {
        // Add body-small class if window less than 768px
        if ($("#___gatsby").width() < 769) {
            $('body').addClass('body-small')
        } else {
            $('body').removeClass('body-small')
        }

        // MetsiMenu
        $('#side-menu').metisMenu();

        // Collapse ibox function
        $('.collapse-link').on('click', function() {
            var ibox = $(this).closest('div.ibox');
            var button = $(this).find('i');
            var content = ibox.children('.ibox-content');
            content.slideToggle(200);
            button.toggleClass('fa-chevron-up').toggleClass('fa-chevron-down');
            ibox.toggleClass('').toggleClass('border-bottom');
            setTimeout(function() {
                ibox.resize();
                ibox.find('[id^=map-]').resize();
            }, 50);
        });

        // Close ibox function
        $('.close-link').on('click', function() {
            var content = $(this).closest('div.ibox');
            content.remove();
        });

        // Fullscreen ibox function
        $('.fullscreen-link').on('click', function() {
            var ibox = $(this).closest('div.ibox');
            var button = $(this).find('i');
            $('body').toggleClass('fullscreen-ibox-mode');
            button.toggleClass('fa-expand').toggleClass('fa-compress');
            ibox.toggleClass('fullscreen');
            setTimeout(function() {
                $("#___gatsby").trigger('resize');
            }, 100);
        });

        // Close menu in canvas mode
        $('.close-canvas-menu').on('click', function() {
            $("body").toggleClass("mini-navbar");
            SmoothlyMenu();
        });

        // Run menu of canvas
        $('body.canvas-menu .sidebar-collapse').slimScroll({height: '100%', railOpacity: 0.9});

        // Open close right sidebar
        $('.right-sidebar-toggle').on('click', function() {
            $('#right-sidebar').toggleClass('sidebar-open');
        });

        // Initialize slimscroll for right sidebar
        $('.sidebar-container').slimScroll({height: '100%', railOpacity: 0.4, wheelStep: 10});

        // Open close small chat
        $('.open-small-chat').on('click', function() {
            $(this).children().toggleClass('fa-comments').toggleClass('fa-remove');
            $('.small-chat-box').toggleClass('active');
        });

        // Initialize slimscroll for small chat
        $('.small-chat-box .content').slimScroll({height: '234px', railOpacity: 0.4});

        // Small todo handler
        $('.check-link').on('click', function() {
            var button = $(this).find('i');
            var label = $(this).next('span');
            button.toggleClass('fa-check-square').toggleClass('fa-square-o');
            label.toggleClass('todo-completed');
            return false;
        });

        this.fix_height();

        // Fixed Sidebar
        $(window).bind("load", function() {
            if ($("body").hasClass('fixed-sidebar')) {
                $('.sidebar-collapse').slimScroll({height: '100%', railOpacity: 0.9});
            }
        });

        // Move right sidebar top after scroll
        $(window).scroll(function() {
            if ($("#___gatsby").scrollTop() > 0 && !$('body').hasClass('fixed-nav')) {
                $('#right-sidebar').addClass('sidebar-top');
            } else {
                $('#right-sidebar').removeClass('sidebar-top');
            }
        });

        $(window).bind("load resize scroll", function() {
            if (!$("body").hasClass('body-small')) {
                let heightWithoutNavbar = $("body > #wrapper").height() - 61;
                $(".sidebar-panel").css("min-height", heightWithoutNavbar + "px");

                let navbarHeigh = $('nav.navbar-default').height();
                let wrapperHeigh = $('#page-wrapper').height();

                if (navbarHeigh > wrapperHeigh) {
                    $('#page-wrapper').css("min-height", navbarHeigh + "px");
                }

                if (navbarHeigh < wrapperHeigh) {
                    $('#page-wrapper').css("min-height", $(window).height() + "px");
                }

                if ($('body').hasClass('fixed-nav')) {
                    if (navbarHeigh > wrapperHeigh) {
                        $('#page-wrapper').css("min-height", navbarHeigh - 60 + "px");
                    } else {
                        $('#page-wrapper').css("min-height", $(window).height() - 60 + "px");
                    }
                }
            }
        });

        $(window).bind("resize", function() {
            if ($("#___gatsby").width() < 769) {
                $('body').addClass('body-small')
            } else {
                $('body').removeClass('body-small')
            }
        });

        $("[data-toggle=popover]").popover();

        // Add slimscroll to element
        $('.full-height-scroll').slimscroll({height: '100%'})
    }

    user_config() {
        if (('localStorage' in window) && window['localStorage'] !== null) {

            var collapse = localStorage.getItem("collapse_menu");
            var fixedsidebar = localStorage.getItem("fixedsidebar");
            var fixednavbar = localStorage.getItem("fixednavbar");
            var boxedlayout = localStorage.getItem("boxedlayout");
            var fixedfooter = localStorage.getItem("fixedfooter");

            var body = $('body');

            if (fixedsidebar == 'on') {
                body.addClass('fixed-sidebar');
                $('.sidebar-collapse').slimScroll({height: '100%', railOpacity: 0.9});
            }

            if (collapse == 'on') {
                if (body.hasClass('fixed-sidebar')) {
                    if (!body.hasClass('body-small')) {
                        body.addClass('mini-navbar');
                    }
                } else {
                    if (!body.hasClass('body-small')) {
                        body.addClass('mini-navbar');
                    }

                }
            }

            if (fixednavbar == 'on') {
                $(".navbar-static-top").removeClass('navbar-static-top').addClass('navbar-fixed-top');
                body.addClass('fixed-nav');
            }

            if (boxedlayout == 'on') {
                body.addClass('boxed-layout');
            }

            if (fixedfooter == 'on') {
                $(".footer").addClass('fixed');
            }
        }
    }

    fix_height() {
        let heightWithoutNavbar = $("body > #wrapper").height() - 61;
        $(".sidebar-panel").css("min-height", heightWithoutNavbar + "px");

        let navbarHeigh = $('nav.navbar-default').height();
        let wrapperHeigh = $('#page-wrapper').height();

        if (navbarHeigh > wrapperHeigh) {
            $('#page-wrapper').css("min-height", navbarHeigh + "px");
        }

        if (navbarHeigh < wrapperHeigh) {
            $('#page-wrapper').css("min-height", $(window).height() + "px");
        }

        if ($('body').hasClass('fixed-nav')) {
            if (navbarHeigh > wrapperHeigh) {
                $('#page-wrapper').css("min-height", navbarHeigh - 60 + "px");
            } else {
                $('#page-wrapper').css("min-height", $(window).height() - 60 + "px");
            }
        }
    }

    SmoothlyMenu(event) {
        event.preventDefault();
        $("body").toggleClass("mini-navbar");

        if (!$('body').hasClass('mini-navbar') || $('body').hasClass('body-small')) {
            // Hide menu in order to smoothly turn on when maximize menu
            $('#side-menu').hide();
            // For smoothly turn on menu
            setTimeout(function() {
                $('#side-menu').fadeIn(400);
            }, 200);
        } else if ($('body').hasClass('fixed-sidebar')) {
            $('#side-menu').hide();
            setTimeout(function() {
                $('#side-menu').fadeIn(400);
            }, 100);
        } else {
            // Remove all inline style from jquery fadeIn function to reset menu state
            $('#side-menu').removeAttr('style');
        }
    }

    render() {
        if (this.props.user.loggedIn) {
            return (
                <div id="wrapper">
                    <Sidebar location={this.props.location} />
                    <div id="page-wrapper" className="gray-bg">
                        <Navigation SmoothlyMenu={this.SmoothlyMenu}/>
                        {this.props.content}
                        <Footer/>
                    </div>
                </div>
            );
        } else {
            return this.props.content;
        }
    }
};

const mapStateToProps = (state) => {
    return {
        user: state.users.user,
        user_experience: state.users.user_experience
    };
}

const mapDispatchToProps = (dispatch) => {
    return {setUser, logOutUser, setUserExperience};
}

export default connect(mapStateToProps, mapDispatchToProps())(DashboardIndex);



