// components / dashboard / sidebar

// Modules
import React from "react";
import {connect} from 'react-redux';
import Link, {navigateTo} from "gatsby-link";

// Action
import {logOutUser} from '../../action/UserAction';

class Sidebar extends React.Component {
    logout() {
        this.props.logOutUser().then((result) => {
            if(result) {
                localStorage.clear();
                navigateTo('/login');
            }
        });
    }

    render() {
        let sidebarStatus = {
                dashboard: 'hidden',
                residence: 'hidden',
                conversation: 'hidden',
                security: 'hidden',
                payment: 'hidden',
                paymentHistory: 'hidden',
                forum: 'hidden',
                facility: 'hidden',
                settings: 'hidden'
            };

        switch (this.props.user.account_type) {
            case 'guard': {
                sidebarStatus.conversation = '';
                sidebarStatus.security = '';
                break;
            }

            case 'creator':
            case 'support':
            case 'manager':
            case 'admin':
            case 'resident': {
                for (let key in sidebarStatus) {
                    sidebarStatus[key] = '';
                }
                break;
            }

            default:
                break;
        }


        return (
            <nav className="navbar-default navbar-static-side" role="navigation">
                <div className="sidebar-collapse">
                    <ul className="nav metismenu" id="side-menu">
                        <li className="nav-header">
                            <div className="dropdown profile-element">
                                <a data-toggle="dropdown" className="dropdown-toggle" href="#">
                                    <span className="clear">
                                        <span className="block m-t-xs">
                                            <strong className="font-bold">Residency.my</strong>
                                        </span>
                                        <span className="text-muted text-xs block">Settings
                                            <b className="caret"></b>
                                        </span>
                                    </span>
                                </a>
                                <ul className="dropdown-menu animated fadeInRight m-t-xs">
                                    <li>
                                        <a href="#" onClick={this.logout.bind(this)}>
                                            Logout
                                        </a>
                                    </li>
                                </ul>
                            </div>
                            <div className="logo-element">
                                .MY
                            </div>
                        </li>
                        <li className={sidebarStatus.dashboard + ' ' + (this.props.location.pathname === '/dashboard' ? 'active' : '') }>
                            <Link to="/dashboard">
                                <i className="fa fa-television"></i>
                                <span className="nav-label">Dashboard</span>
                            </Link>
                        </li>
                        <li className={sidebarStatus.residence + ' ' + (this.props.location.pathname.indexOf('residence') > -1 ? 'active' : '') }>
                            <Link to="/dashboard/residence">
                                <i className="fa fa-home"></i>
                                <span className="nav-label">My Residence</span>
                            </Link>
                        </li>
                        <li className={(this.props.location.pathname.indexOf('announcement') > -1 ? 'active' : '') }>
                            <Link to="/dashboard/announcement/manage">
                                <i className="fa fa-bullhorn"></i>
                                <span className="nav-label">Announcement</span>
                            </Link>
                        </li>
                        <li className={sidebarStatus.conversation + ' ' + (this.props.location.pathname.indexOf('conversation') > -1 ? 'active' : '') }>
                            <Link to="/dashboard/conversation">
                                <i className="fa fa-envelope-o"></i>
                                <span className="nav-label">Conversation</span>
                            </Link>
                        </li>
                        <li className={sidebarStatus.security + ' ' + (this.props.location.pathname.indexOf('security') > -1 ? 'active' : '') }>
                            <Link to="/dashboard/security">
                                <i className="fa fa-lock"></i>
                                <span className="nav-label">Security Cameras</span>
                            </Link>
                        </li>
                        <li className={sidebarStatus.payment + ' ' + (this.props.location.pathname === '/dashboard/payment' ? 'active' : '') }>
                            <Link to="/dashboard/payment">
                                <i className="fa fa-dollar"></i>
                                <span className="nav-label">Payment</span>
                            </Link>
                        </li>
                        <li className={sidebarStatus.paymentHistory + ' ' + (this.props.location.pathname.indexOf('payment-history') > -1 ? 'active' : '') }>
                            <Link to="/dashboard/payment-history">
                                <i className="fa fa-list-alt"></i>
                                <span className="nav-label">Payment History</span>
                            </Link>
                        </li>
                        <li className={sidebarStatus.forum + ' ' + (this.props.location.pathname.indexOf('forum') > -1 ? 'active' : '') }>
                            <Link to="/dashboard/forum">
                                <i className="fa fa-bullhorn"></i>
                                <span className="nav-label">Forum Board</span>
                            </Link>
                        </li>
                        <li className={sidebarStatus.facility + ' ' + (this.props.location.pathname.indexOf('facility') > -1 ? 'active' : '') }>
                            <a href="#"><i className="fa fa-building-o"></i> <span className="nav-label">Facility</span><span className="fa arrow"></span></a>
                            <ul className="nav nav-second-level collapse">
                                <li className={(this.props.location.pathname.indexOf('facility/booking') > -1 ? 'active' : '') }>
                                    <Link to="/dashboard/facility/booking">Booking</Link>
                                </li>
                                <li className={(this.props.location.pathname.indexOf('facility/manage-facility') > -1 ? 'active' : '') }>
                                    <Link to="/dashboard/facility/manage-facility">Manage Facility</Link>
                                </li>
                                <li className={(this.props.location.pathname.indexOf('facility/manage-booking') > -1 ? 'active' : '') }>
                                    <Link to="/dashboard/facility/manage-booking">Manage Booking</Link>
                                </li>
                            </ul>
                        </li>
                        <li className={(this.props.location.pathname.indexOf('property') > -1 ? 'active' : '') }>
                            <Link to="/dashboard/property/manage">
                                <i className="fa fa-building-o"></i>
                                <span className="nav-label">Property Mgmt</span>
                            </Link>
                        </li>
                        <li className={(this.props.location.pathname.indexOf('account') > -1 ? 'active' : '') }>
                            <a href="#"><i className="fa fa-users"></i> <span className="nav-label">Account Mgmt</span><span className="fa arrow"></span></a>
                            <ul className="nav nav-second-level collapse">
                                <li className={(this.props.location.pathname.indexOf('account/manage') > -1 ? 'active' : '') }>
                                    <Link to="/dashboard/account/manage">Manage Accounts</Link>
                                </li>
                                <li  className={(this.props.location.pathname.indexOf('account/add') > -1 ? 'active' : '') }>
                                    <Link to="/dashboard/account/add">Add Account</Link>
                                </li>
                            </ul>
                        </li>
                        <li className={(this.props.location.pathname.indexOf('cost-structure') > -1 ? 'active' : '') }>
                            <Link to="/dashboard/cost-structure">
                                <i className="fa fa-calculator"></i>
                                <span className="nav-label">Cost Structure</span>
                            </Link>
                        </li>
                        <li className={sidebarStatus.settings + ' ' + (this.props.location.pathname === '/dashboard/settings' ? 'active' : '') }>
                            <Link to="/dashboard/settings">
                                <i className="fa fa-cogs"></i>
                                <span className="nav-label">Settings</span>
                            </Link>
                        </li>
                    </ul>
                </div>
            </nav>
        );
    }
};

const mapStateToProps = (state) => {
    return {
        user: state.users.user
    };
}

const mapDispatchToProps = (dispatch) => {
    return {logOutUser};
}

export default connect(mapStateToProps, mapDispatchToProps())(Sidebar);



