// components / dashboard / navigation

// Modules
import React from "react";
import {connect} from 'react-redux';
import Link, {navigateTo} from "gatsby-link";

// Action
import {logOutUser} from '../../action/UserAction';

class Navigation extends React.Component {
    logout() {
        this.props.logOutUser().then((result) => {
            if(result) {
                localStorage.clear();
                navigateTo('/login');
            }
        });
    }

    render() {
        return (
            <div className="row border-bottom">
                <nav className="navbar navbar-static-top white-bg" role="navigation" style={{marginBottom: 0}}>
                    <div className="navbar-header">
                        <a className="navbar-minimalize minimalize-styl-2 btn btn-primary " href="#" onClick={this.props.SmoothlyMenu.bind(this)}>
                            <i className="fa fa-bars"></i>
                        </a>
                        <form role="search" className="navbar-form-custom" method="post" action="#">
                            <div className="form-group">
                                <input type="text" placeholder="Search for something..." className="form-control" name="top-search" id="top-search"/>
                            </div>
                        </form>
                    </div>
                    <ul className="nav navbar-top-links navbar-right">
                        <li className="dropdown">
                            <a className="dropdown-toggle count-info" data-toggle="dropdown" href="#">
                                <i className="fa fa-bell"></i>  <span className="label label-primary">8</span>
                            </a>
                            <ul className="dropdown-menu dropdown-alerts">
                                <li>
                                    <a href="#">
                                        <div>
                                            <i className="fa fa-envelope fa-fw"></i> You have 16 messages
                                            <span className="pull-right text-muted small">4 minutes ago</span>
                                        </div>
                                    </a>
                                </li>
                                <li className="divider"></li>
                                <li>
                                    <a href="#">
                                        <div>
                                            <i className="fa fa-list-alt"></i> Water Bill due soon
                                            <span className="pull-right text-muted small">12 minutes ago</span>
                                        </div>
                                    </a>
                                </li>
                                <li className="divider"></li>
                                <li>
                                    <a href="#">
                                        <div>
                                            <i className="fa fa-list-alt"></i> Electric Bill due soon
                                            <span className="pull-right text-muted small">12 minutes ago</span>
                                        </div>
                                    </a>
                                </li>
                                <li className="divider"></li>
                                <li>
                                    <div className="text-center link-block">
                                        <a href="#">
                                            <strong>See All Alerts</strong> <i className="fa fa-angle-right"></i>
                                        </a>
                                    </div>
                                </li>
                            </ul>
                        </li>
                        <li>
                            <a href="#" onClick={this.logout.bind(this)}>
                                <i className="fa fa-sign-out"></i>
                                Log out
                            </a>
                        </li>
                    </ul>
                </nav>
            </div>
        );
    }
};

const mapStateToProps = (state) => {
    return {
        user: state.users.user
    };
}

const mapDispatchToProps = (dispatch) => {
    return {logOutUser};
}

export default connect(mapStateToProps, mapDispatchToProps())(Navigation);



